import React, { useState } from "react";
import "./style.css";

export default function SearchMenuComponent(props) {
  const {
    placeholder,
    handleChangeText,
    id,
    activeInput,
    handleChangeActiveInput
  } = props;
  const flag = activeInput === id;
  const [value, setValue] = useState("");

  return (
    <div>
      <input
        className={`input-text ${flag ? "input-active" : ""}`}
        type="text"
        placeholder={placeholder}
        value={flag ? value : ""}
        data-name={placeholder}
        id={id}
        onChange={e => {
          setValue(e.currentTarget.value);
          handleChangeText({
            filter: e.currentTarget.value,
            inputName: e.currentTarget.dataset.name
          });
          flag ? console.log("–∑–∞–≥–ª—É—à–∫–∞") : handleChangeActiveInput(id);
        }}
      />
    </div>
  );
}
