import React from "react";

import { SearchMenuContainer } from "../../containers/SearchMenuContainer";
import { SortMenuContainer } from "../../containers/SortMenuContainer";

import "./style.css";

export default function GroupMenuComponent(props) {
  const { placeholder, id } = props;

  return (
    <div>
      <SearchMenuContainer placeholder={placeholder} id={id} />
      <SortMenuContainer placeholder={placeholder} />
    </div>
  );
}
