import React from "react";
import "./style.css";
import GroupMenuComponent from "../GroupMenuComponent";

export default function HeadComponent(props) {
  const { data } = props;
  return (
    <thead>
      <tr>
        {Object.keys(data).map((item, id) => (
          <th className="col" key={id}>
            {item}
            <GroupMenuComponent placeholder={item} id={id} />
          </th>
        ))}
      </tr>
    </thead>
  );
}
