import React from "react";
import CellBodyComponent from "../CellBodyComponent";
import "./style.css";

export default function RowComponent(props) {
  const { content, id } = props;

  return (
    <tr className={`${id % 2 ? "" : "rowComp-white"}`} key={id}>
      {Object.keys(content).map((item, id) => (
        <CellBodyComponent value={content[item]} key={id} />
      ))}
    </tr>
  );
}
