import { connect } from "react-redux";
import InputBtnClearComponent from "../../components/InputBtnClearComponent";
import { changeClear } from "../../store/actions";

const mapStateToProps = state => ({});

const mapDispatchToProps = dispatch => ({
  handleClear: flag => dispatch(changeClear(flag))
});

export const InputBtnClearContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(InputBtnClearComponent);

