import React from "react";
import RowComponent from "../RowComponent";
// import {sortNumber, sortString, sortDate} from '../../utils/sortData';
import SortCustom from '../../utils/sortData';

export default function BodyComponent(props) {
  const { data, sort, nameInput } = props;
  const sortCustom = new SortCustom(data, nameInput, sort)

  return (
    <tbody>
      {[...sortCustom].length ? (
          [...sortCustom].map((item, id) => (
            <RowComponent content={item} id={id} key={id} />
          ))
        ) : (
        <RowComponent content={["–Ω–µ—Ç —Å–æ–≤–ø–∞–¥–µ–Ω–∏–π"]} />
      )}
    </tbody>
  );
}
