import React from "react";
import InputBtnSortComponent from "../InputBtnSortComponent";
import { arrBtn } from "../../utils/arrBtn";
import "./style.css";

export default function SortMenuComponent(props) {
  const { handleChangeSort, placeholder } = props;

  return (
    <div className="sortMenuComp">
      {arrBtn.map((item, id) => (
        <InputBtnSortComponent
          value={item.icon}
          flag={item.flag}
          handleChangeSort={handleChangeSort}
          placeholder={placeholder}
          key={id}
          id={id}
        />
      ))}
    </div>
  );
}
