export const initialState = {
  content: [...contentTable],
  changeContent: [],
  sort: "default",
  nameInput: "",
  text: {
    filter: "",
    inputName: ""
  },
  activeInput: null
};

export function tableReducers(state = initialState, action) {
  switch (action.type) {
    case CHANGE_TEXT:
      return { ...state, ...{ text: action.data } };

    case CHANGE_SORT:
      const { flag, placeholder } = action.data;
      return { ...state, ...{ sort: flag, nameInput: placeholder } };

    case CHANGE_ACTIVE_INPUT:
      return { ...state, ...{ activeInput: action.data } };

    case CHANGE_CLEAR:
      return {
        ...state,
        ...{
          sort: action.data,
          activeInput: action.data,
          text: { filter: action.data, inputName: action.data }
        }
      };

    default:
      return state;
  }
}
