import React from "react";

export default function InputBtnSortComponent(props) {
  const { value, handleChangeSort, flag, id, placeholder } = props;
  return (
    <div>
      <input
        className="inputBtnComp"
        type="button"
        value={value}
        data-placeholder={placeholder}
        data-id={id}
        onClick={() => {
          handleChangeSort({ flag, placeholder });
        }}
      />
    </div>
  );
}
