import { connect } from "react-redux";
import TableWidgetComponent from "../../components/TableWidgetComponent";
import { changeText } from "../../store/actions";

const mapStateToProps = state => ({
  state: state
});

const mapDispatchToProps = dispatch => ({
  handleChange: text => dispatch(changeText(text))
});

export const TableWidgetContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(TableWidgetComponent);
