import { connect } from "react-redux";
import SearchMenuComponent from "../../components/SearchMenuComponent";
import { changeText, changeActiveInput } from "../../store/actions";

const mapStateToProps = state => ({
  activeInput: state.content.activeInput
});

const mapDispatchToProps = dispatch => ({
  handleChangeText: (text, name) => dispatch(changeText(text, name)),
  handleChangeActiveInput: id => dispatch(changeActiveInput(id))
});

export const SearchMenuContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchMenuComponent);
